<template lang="pug">
  include ./../../../mixins
  div
    router-module()
    +b.pa--t_md.--b_md.SECTION
      +b.ug-container--variant_large
        +b.TEXT-BEMTO(
          :fw="'bold'"
          :lh="'small'"
          :fs="'xl'"
          :color="'black'"
          :tag="'p'"
        ) ORDER HISTORY
        +b.ug-row--apearance_spaced.--space_md
          +b.ug-cell.ug-cols--12
            +b.pa--f_1sm.--f_md-md.bg--white.ma--t_md
              validation-observer(
                ref="observer"
                tag="form"
                class="form"
              )
                +b.ug-row--apearance_spaced.--space_md.--justify_between.--align_end
                  +b.ug-cell.ug-cols--12.--6-mac
                    +b.control-cabinet.--centered
                      +e.label Date and time of order
                    +b.ug-row--apearance_spaced.--space_sm.--align_end
                      +b.ug-cell.ug-cols--12.--auto-md
                        +b.control-cabinet.--centered
                          +e.element--centered.FLAT-PICKER(
                            v-model='createdAt.dateGte', :config="cfgDate",
                            placeholder='DD/MM/YYYY',
                            type="date",
                          )
                          +e.flat-toggle(
                            title="toggle"
                            data-toggle
                          )
                            +b.icon--type_date.I
                      +b.ug-cell.ug-cols--12.--auto-md
                        +b.control-cabinet.--centered
                          +e.element--centered.FLAT-PICKER(
                            v-model='createdAt.timeGte', :config="cfgTime",
                            placeholder='HH:MM K',
                            type="date",
                            :disabled="!createdAt.dateGte.length"
                          )
                          +e.flat-toggle(
                            title="toggle"
                            data-toggle
                          )
                            +b.icon--type_time.I
                      +b.ug-cell.ug-cols--12.--narrow-md.ma--off
                        +b.TEXT-BEMTO(
                          :cfg="'datetime'"
                          :fw="'bold'"
                          :lh="'small'"
                          :fs="'1xs'"
                          :color="'black'"
                          :tag="'p'"
                        ) to
                      +b.ug-cell.ug-cols--12.--auto-md
                        +b.control-cabinet.--centered
                          +e.element--centered.FLAT-PICKER(
                            v-model='createdAt.dateLte', :config="cfgDate",
                            placeholder='DD/MM/YYYY',
                            type="date",
                          )
                          +e.flat-toggle(
                            title="toggle"
                            data-toggle
                          )
                            +b.icon--type_date.I
                      +b.ug-cell.ug-cols--12.--auto-md
                        +b.control-cabinet.--centered
                          +e.element--centered.FLAT-PICKER(
                            v-model='createdAt.timeLte', :config="cfgTime",
                            placeholder='HH:MM K',
                            type="date",
                            :disabled="!createdAt.dateLte.length"
                          )
                          +e.flat-toggle(
                            title="toggle"
                            data-toggle
                          )
                            +b.icon--type_time.I
                  +b.ug-cell.ug-cols--12.--6-mac
                    +b.control-cabinet.--centered
                      +e.label Date and time of booking
                    +b.ug-row--apearance_spaced.--space_sm.--align_end
                      +b.ug-cell.ug-cols--12.--auto-md
                        +b.control-cabinet.--centered
                          +e.element--centered.FLAT-PICKER(
                            v-model='dateAt.dateGte', :config="cfgDate",
                            placeholder='DD/MM/YYYY',
                            type="date",
                          )
                          +e.flat-toggle(
                            title="toggle"
                            data-toggle
                          )
                            +b.icon--type_date.I
                      +b.ug-cell.ug-cols--12.--auto-md
                        +b.control-cabinet.--centered
                          +e.element--centered.FLAT-PICKER(
                            v-model='dateAt.timeGte', :config="cfgTime",
                            placeholder='HH:MM K',
                            type="date",
                            :disabled="!dateAt.dateGte.length"
                          )
                          +e.flat-toggle(
                            title="toggle"
                            data-toggle
                          )
                            +b.icon--type_time.I
                      +b.ug-cell.ug-cols--12.--narrow-md.ma--off
                        +b.TEXT-BEMTO(
                          :cfg="'datetime'"
                          :fw="'bold'"
                          :lh="'small'"
                          :fs="'1xs'"
                          :color="'black'"
                          :tag="'p'"
                        ) to
                      +b.ug-cell.ug-cols--12.--auto-md
                        +b.control-cabinet.--centered
                          +e.element--centered.FLAT-PICKER(
                            v-model='dateAt.dateLte', :config="cfgDate",
                            placeholder='DD/MM/YYYY',
                            type="date",
                          )
                          +e.flat-toggle(
                            title="toggle"
                            data-toggle
                          )
                            +b.icon--type_date.I
                      +b.ug-cell.ug-cols--12.--auto-md
                        +b.control-cabinet.--centered
                          +e.element--centered.FLAT-PICKER(
                            v-model='dateAt.timeLte', :config="cfgTime",
                            placeholder='HH:MM K',
                            type="date",
                            :disabled="!dateAt.dateLte.length"
                          )
                          +e.flat-toggle(
                            title="toggle"
                            data-toggle
                          )
                            +b.icon--type_time.I
                +b.ug-row--apearance_spaced.--space_md.--justify_between.--align_end
                  +b.ug-cell.ug-cols--12.--6-md
                    +b.ug-row--apearance_spaced.--space_sm.--space_md-mac
                      +b.ug-cell.ug-cols--12.--6-md
                        +b.input.D-CONTROL-INPUT(
                          v-model="formData.search"
                          :input-label="'Order ID'"
                          :placeholder="'Order ID'"
                          :name="'search'"
                          :type="'text'"
                          id="search"
                        )
                      +b.ug-cell.ug-cols--12.--6-md
                        +b.D-CONTROL-OPTION(
                          v-model="formData.paid"
                          :object-list="depOptions"
                          :input-label="'Deposit status'"
                          :placeholder="'Deposit status'"
                          :name="'paid'"
                          id="paid"
                        )
                  +b.ug-cell.ug-cols--12.--6-md
                    +b.ug-row--apearance_spaced.--space_sm.--space_md-mac
                      +b.ug-cell.ug-cols--12.--6-sm.--7-xl
                        +b.button--height_xl.BUTTON-BEMTO(
                          :disabled="isRequest"
                          @event="resetPaginationBeforeAPI"
                        )
                          +e.load.IMG(
                            src="/static/load.gif"
                            v-if="isRequest"
                          )
                          +e.text(
                            v-if="!isRequest"
                          ) SEARCH
                      +b.ug-cell.ug-cols--12.--6-sm.--5-xl
                        +b.button--height_xl.--save.BUTTON-BEMTO(
                          :disabled="isRequest"
                          @event="clearInvoiceFiltersHandler"
                        )
                          +e.load.IMG(
                            src="/static/load.gif"
                            v-if="isRequest"
                          )
                          +e.text(
                            v-if="!isRequest"
                          ) CLEAR
            +b.pa--r_1sm.--r_md-md.--l_1sm.--l_md-md.--b_1sm.bg--white
              order-history-table(
                v-if="Object.keys(orderItems).length"
                :object-list="orderItems"
                @ordering="setOrderingHandler"
              )
              +b.text.pa--b_xs-md(
                v-if="!Object.keys(orderItems).length"
              ) No results
          +b.ug-cell.ug-cols--12
            +b.PAGINATION-MODULE(
              :pagination="pagination"
              :extra="1"
              :need-add-btns="true"
              @update-limit="setPaginationLimitHandler($event)"
              @input="pageСhangedHandler($event)"
            )
</template>

<script>
import {
  getOrdersHistoryAPI,
  getOrdersStatusAPI,
} from '@/js/services/cabinet'

import {
  asyncForEach,
} from '@/js/scripts/helpers'

import {
  mask
} from 'vue-the-mask'

export default {
  name: 'OrderHistoryCabinet',
  directives: {
    mask,
  },
  created() {
    this.getOrdersHistoryAPI()
    this.getOrdersStatusAPI()
  },
  watch: {
    'createdAt.dateGte'(val) {
      if (!val.length) {
        this.createdAt.timeGte = ''
      }
    },
    'createdAt.dateLte'(val) {
      if (!val.length) {
        this.createdAt.timeLte = ''
      }
    },
    'dateAt.dateGte'(val) {
      if (!val.length) {
        this.dateAt.timeGte = ''
      }
    },
    'dateAt.dateLte'(val) {
      if (!val.length) {
        this.dateAt.timeLte = ''
      }
    },
  },
  data() {
    return {
      /** */
      cfgDate: {
        dateFormat: 'd/m/Y',
        allowInput: true,
        wrap: true,
      },
      /** */
      cfgTime: {
        dateFormat: 'h:i K',
        enableTime: true,
        noCalendar: true,
        allowInput: true,
        wrap: true,
      },
      /** */
      formData: {
        search: null,
        paid: null,
        ordering: null,
      },
      isRequest: false,
      /** */
      createdAt: {
        dateGte: '',
        dateLte: '',
        timeGte: '',
        timeLte: '',
      },
      dateAt: {
        dateGte: '',
        dateLte: '',
        timeGte: '',
        timeLte: '',
      },
      inits: 1,
      /** */
      depOptions: [],
      orderItems: [],
      pagination: {},
      paginationData: {},
    }
  },
  methods: {
    /**
     * Our request with get parameters, not to
     * be confused with CNC, here we need
     * special processing of the server
     * response.
     * @params {Object} OPTIONS
     */
    getOrdersHistoryAPI(OPTIONS) {
      return getOrdersHistoryAPI.execute(OPTIONS)
        .then(response => response.json())
        .then(response => {
          this.orderItems = response.data.items
          this.pagination = response.data.pagination
        })
    },
    /**
     * Getting Filters for the Deposit State.
     */
    getOrdersStatusAPI() {
      return getOrdersStatusAPI.execute({})
        .then(response => {
          this.depOptions = response.data.item
        })
    },
    /**
     * Let's pre-format our form state.
     */
    async preperedBeforeAPI() {
      /**
       * Create the correct format for sending the date
       * and time to the server.
       */
      const DATETIME = {
        'created_at__gte': `${this.createdAt.dateGte} ${this.createdAt.timeGte}`,
        'created_at__lte': `${this.createdAt.dateLte} ${this.createdAt.timeLte}`,
        'date_at__gte': `${this.dateAt.dateGte} ${this.dateAt.timeGte}`,
        'date_at__lte': `${this.dateAt.dateLte} ${this.dateAt.timeLte}`,
      }
      /**
       * The task is not to send only the time to the server,
       * the date is also required, so we will delete
       * the keys in which there is no date, so as not
       * to send them.
       */
      await asyncForEach(
        Object.keys(DATETIME), async key => {
          /**
           * The some () method checks if any element of
           * the array satisfies the condition specified
           * in the passed function.
           */
          if (DATETIME[key].length <= this.inits) {
            delete DATETIME[key]
          }
        }
      )
      /**
       * We merge all the data thatwe have in thefilters and
       * finish the preliminarykey formatting.
       */
      const OPTIONS = {
        ...this.formData,
        ...this.paginationData,
        ...DATETIME
      }
      /**
       * We will wait until everything is filled correctly.
       */
      await asyncForEach(
        Object.keys(OPTIONS), async key => {
          /**
           * The some () method checks if any element of
           * the array satisfies the condition specified
           * in the passed function.
           */
          if (!OPTIONS[key]) {
            delete OPTIONS[key]
          }
        }
      )

      console.log(OPTIONS)

      /**
       * Pay attention to the description of this method.
       */
      await this.getOrdersHistoryAPI(
        OPTIONS
      )
    },
    async clearInvoiceFiltersHandler() {
      await asyncForEach(
        Object.keys(this.formData), async key => {
          this.formData[key] = null
        }
      )
      await asyncForEach(
        Object.keys(this.createdAt), async key => {
          this.createdAt[key] = ''
        }
      )
      await asyncForEach(
        Object.keys(this.dateAt), async key => {
          this.dateAt[key] = ''
        }
      )
      this.paginationData.offset = 0
      await this.preperedBeforeAPI()
    },
    async resetPaginationBeforeAPI() {
      this.paginationData.offset = 0
      await this.preperedBeforeAPI()
    },
    async setPaginationLimitHandler(value) {
      this.paginationData.limit = value
      this.paginationData.offset = 0
      await this.preperedBeforeAPI()
    },
    async pageСhangedHandler(data) {
      this.paginationData = data
      await this.preperedBeforeAPI()
    },
    async setOrderingHandler(value) {
      this.formData.ordering = value
      await this.preperedBeforeAPI()
    },
  },
}
</script>



