<template lang="pug">
  include ./../../../../mixins
  validation-observer(
    ref="observer"
    tag="form"
    class="form"
  )
    +b.ug-row--apearance_spaced.--space_md
      +b.ug-cell.ug-cols--12.--6-md
        +b.input.D-CONTROL-INPUT(
          v-model="formData.companyName"
          :input-label="'Company name *'"
          :placeholder="'Company name *'"
          :rules="'required'"
          :name="'companyName'"
          :type="'text'"
          id="companyName"
        )
      +b.ug-cell.ug-cols--12.--6-md
        +b.input.D-CONTROL-INPUT(
          v-model="formData.phone"
          :input-label="'Phone number *'"
          :placeholder="'Phone number *'"
          :rules="'required|ukPhone'"
          :name="'phone'"
          :type="'text'"
          id="phone"
        )
      +b.ug-cell.ug-cols--12.--6-md
        +b.input.D-CONTROL-INPUT(
          v-model="formData.firstName"
          :input-label="'First name'"
          :placeholder="'First name'"
          :rules="'required'"
          :name="'firstName'"
          :type="'text'"
          id="firstName"
        )
      +b.ug-cell.ug-cols--12.--6-md
        +b.input.D-CONTROL-INPUT(
          v-model="formData.lastName"
          :input-label="'Last name'"
          :placeholder="'Last name'"
          :rules="'required'"
          :name="'lastName'"
          :type="'text'"
          id="lastName"
        )
    +b.ug-row--appearance_spaced.--space_md.--justify_end
      +b.ug-cell.ug-cols--12.--6-md.--3-mac
        +b.button--height_xl.BUTTON-BEMTO(
          :disabled="isRequest"
          @event="validateBeforeSubmit(formData)"
        )
          +e.load.IMG(
            src="/static/load.gif"
            v-if="isRequest"
          )
          +e.text(
            v-if="!isRequest"
          ) SAVE
</template>

<script>
import FormMixin, { defaultValidatorErrorsParser } from '@/js/mixins/FormMixin'
import ToggleModalMixin from '@/js/mixins/ToggleModalMixin'
import { postCommonChangeAPI } from '@/js/services/cabinet'
import { mapGetters } from 'vuex'

export default {
  name: 'CommonChange',
  mixins: [
    FormMixin,
    ToggleModalMixin,
  ],
  data() {
    return {
      /**
       * Component state.
       */
      formData: {
        companyName: null,
        phone: null,
        firstName: null,
        lastName: null,
      },
      isRequest: false,
    }
  },
  computed: {
    ...mapGetters('cabinet', [
      'profileCommon',
    ]),
  },
  watch: {
    /**
     * Hook with updating personal
     * data, listening to the data,
     * if any, then we react.
     */
    profileCommon: {
      immediate: true,
      deep: true,
      handler(data) {
        /**
         * If any, then we react.
         */
        if (data) {
          this.prefillData(data)
        }
      },
    },
  },
  methods: {
    /**
     * Submission handler, if validated.
     * @param { Object } data - Send data.
     */
    async send(data) {
      await postCommonChangeAPI.execute(
        {}, data
      )
        .then(response => {
          const OPTIONS = {
            title: response.data.item.detail
          }
          /**
           * Show success message.
           */
          this.toggleModalMixin(
            'SuccessCommon', OPTIONS
          )
        })
    },
    /** */
    prefillData(data) {
      Object.keys(data).forEach(key => {
        this.formData[key] = data[key]
      })
    },
    /**
     * After > error display normalization >
     * Set Observer errors.
     *
     * @param { Object } errors - Formatted errors
     */
    setErrors(errors) {
      const error = defaultValidatorErrorsParser(errors)
      this.$refs.observer.setErrors(error)
    },
  },
}
</script>



