<template lang="pug">
  include ./../../mixins
  +b.table-invoice
    +e.TABLE
      +e.thead.THEAD
        +e.TR
          +e.th--id.TH
            +e.info-wrapper--pointer(
              @click.prevent="changeSort('id')"
            )
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) ID
              +e.icon.icon--type_order
          +e.th.TH
            +e.info-wrapper--pointer(
              @click.prevent="changeSort('order')"
            )
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) ORDER ID
              +e.icon.icon--type_order
          +e.th.TH
            +e.info-wrapper--pointer(
              @click.prevent="changeSort('withdraw')"
            )
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) INVOICE ID
              +e.icon.icon--type_order
          +e.th.TH
            +e.info-wrapper--pointer(
              @click.prevent="changeSort('created_at')"
            )
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) TRANSACTION DATE
              +e.icon.icon--type_order
          +e.th.TH
            +e.info-wrapper
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) TYPE
          +e.th.TH
            +e.info-wrapper--pointer(
              @click.prevent="changeSort('amount')"
            )
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) AMOUNT
              +e.icon.icon--type_order
          +e.th.TH
            +e.info-wrapper--pointer(
              @click.prevent="changeSort('balance')"
            )
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) BALANCE
              +e.icon.icon--type_order
      +e.TBODY
        +e.TR(
          v-for="(item, index) in objectList"
          :key="`${item.id}/${index}`"
        )
          +e.td--id.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.id || '-' }}
          +e.td.TD
            +e.info-wrapper
              +b.text--fw_medium.--fs_xs.--color_blue.ROUTER-LINK(
                :to="{ name: 'BalanceDetailsCabinet', params: { from: 'balance', id: item.order } }"
                v-if="item.order"
              ) {{ item.order }}
              +b.TEXT-BEMTO(
                v-if="!item.order"
                :fw="'medium'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) -
          +e.td.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.withdraw || '-' }}
          +e.td.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.createdAt || '-' }}
          +e.td.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.transactionsType || '-' }}
          +e.td.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.summ || '-' }}
          +e.td.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.balance || '-' }}
</template>

<script>
export default {
  name: 'BalanceHistoryTable',
  props: [
    'objectList',
  ],
  data() {
    return {
      ordering: '-',
    }
  },
  methods: {
    setOrderHandler(key) {
      if (this.ordering.includes('-')) {
        this.ordering = key
      } else {
        this.ordering = `-${key}`
      }
    },
    async changeSort(key) {
      await this.setOrderHandler(key)
      this.$emit('ordering', this.ordering)
    },
  },
}
</script>



