<template lang="pug">
  include ./../../../mixins
  +b.auth
    validation-observer(
      ref="observer"
      tag="form"
      class="form"
    )
      +b.ma--b_sm.TEXT-BEMTO(
        :fw="'bold'"
        :lh="'initial'"
        :fs="'4xl'"
        :color="'black'"
        :tag="'p'"
      ) Create new password
      +b.ma--b_sm.TEXT-BEMTO(
        :fw="'medium'"
        :lh="'small'"
        :fs="'1md'"
        :color="'black'"
        :tag="'p'"
      ) Your new password must be at least 8 characters long and contain at least 6 unique characters
      validation-provider(
        v-slot="{ errors }"
        name="nonFieldErrors"
      )
        +b.ma--b_sm.TEXT-BEMTO(
          :fw="'medium'"
          :lh="'small'"
          :fs="'xs'"
          :color="'red'"
          :tag="'p'"
        ) {{ errors[0] }}
      +b.ug-row--apearance_spaced.--space_md
        +b.ug-cell.ug-cols--12
          +b.input--modal.D-CONTROL-INPUT(
            v-model="formData.newPassword1"
            :input-label="'Create new password *'"
            :placeholder="'••••••••'"
            :rules="'required|min:8|max:16'"
            :name="'newPassword1'"
            :type="'password'"
            id="newPassword1"
          )
        +b.ug-cell.ug-cols--12
          +b.input--modal.D-CONTROL-INPUT(
            v-model="formData.newPassword2"
            :input-label="'Confirm new password *'"
            :placeholder="'••••••••'"
            :rules="'required|min:8|max:16|confirmed:newPassword1'"
            :name="'newPassword2'"
            :type="'password'"
            id="newPassword2"
          )
        +b.ug-cell.ug-cols--12
          +b.button.BUTTON-BEMTO(
            :disabled="isRequest"
            @event="validateBeforeSubmit(formData)"
          ) Submit
</template>

<script>
import FormMixin, { defaultValidatorErrorsParser } from '@/js/mixins/FormMixin'
import ToggleModalMixin from '@/js/mixins/ToggleModalMixin'
import { postNewPassCabinetAPI } from '@/js/services/cabinet'

export default {
  name: 'ResetCabinet',
  mixins: [
    FormMixin,
    ToggleModalMixin
  ],
  props: [
    'token',
    'uid',
  ],
  data() {
    return {
      /**
       * Component state.
       */
      formData: {
        token: this.token,
        uid: this.uid,
        newPassword1: null,
        newPassword2: null,
      },
      isRequest: false,
    }
  },
  methods: {
    /**
     * Submission handler, if validated.
     * @param { Object } data - Send data.
     */
    async send(data) {
      await postNewPassCabinetAPI.execute(
        {}, data
      )
        .then(response => {
          const OPTIONS = {
            title: response.data.item.detail,
            url: response.data.item.redirect,
          }
          /**
           * Show success message.
           */
          this.toggleModalMixin(
            'RedirectModal', OPTIONS
          )
        })
    },
    /**
     * After > error display normalization >
     * Set Observer errors.
     *
     * @param { Object } errors - Formatted errors
     */
    setErrors(errors) {
      const error = defaultValidatorErrorsParser(errors)
      this.$refs.observer.setErrors(error)
    },
  },
}
</script>



