<template lang="pug">
  include ./../../../mixins
  +b.auth
    validation-observer(
      ref="observer"
      tag="form"
      class="form"
    )
      +b.ma--b_xs.TEXT-BEMTO(
        :fw="'bold'"
        :lh="'initial'"
        :fs="'4xl'"
        :color="'black'"
        :tag="'p'"
      ) Login
      +b.ma--b_sm.TEXT-BEMTO(
        :fw="'medium'"
        :lh="'small'"
        :fs="'md'"
        :color="'black'"
        :tag="'p'"
      ) Enter your details below
      validation-provider(
        v-slot="{ errors }"
        name="nonFieldErrors"
      )
        +b.mb-15.ma--b_md.TEXT-BEMTO(
          :fw="'medium'"
          :lh="'small'"
          :fs="'xs'"
          :color="'red'"
          :tag="'p'"
        ) {{ errors[0] }}
      +b.ug-row--apearance_spaced.--space_md
        +b.ug-cell.ug-cols--12
          +b.input.D-CONTROL-INPUT(
            v-model="formData.username"
            :input-label="'Username *'"
            :placeholder="'Username *'"
            :rules="{ required: true }"
            :name="'username'"
            :type="'text'"
            id="username"
          )
        +b.ug-cell.ug-cols--12
          +b.input.D-CONTROL-INPUT(
            v-model="formData.password"
            :input-label="'Password *'"
            :placeholder="'Password *'"
            :rules="'required|min:8|max:16'"
            :name="'password'"
            :type="'password'"
            id="password"
          )
        +b.ug-cell.ug-cols--12
          +b.ug-row--apearance_spaced_md.--justify_end
            +b.ug-cell
              +b.TEXT-BEMTO(
                :cfg="['underline', 'pointer']"
                :fw="'medium'"
                :lh="'initial'"
                :fs="'xs'"
                :color="'gray1'"
                :tag="'p'"
                @event="toggleModalMixin('RestoreCabinet')"
              ) Forgot your password?
        +b.ug-cell.ug-cols--12
          +b.button.BUTTON-BEMTO(
            :disabled="isRequest"
            @event="validateBeforeSubmit(formData)"
          ) Login
</template>

<script>
import FormMixin, { defaultValidatorErrorsParser } from '@/js/mixins/FormMixin'
import ToggleModalMixin from '@/js/mixins/ToggleModalMixin'
import { postLoginCabinetAPI } from '@/js/services/cabinet'

export default {
  name: 'LoginCabinet',
  mixins: [
    FormMixin,
    ToggleModalMixin
  ],
  data() {
    return {
      /**
       * Component state.
       */
      formData: {
        username: null,
        password: null,
      },
      isRequest: false,
    }
  },
  methods: {
    /**
     * Submission handler, if validated.
     * @param { Object } data - Send data.
     */
    async send(data) {
      await postLoginCabinetAPI.execute(
        {}, data
      )
        .then(response => {
          document.location.reload()
        })
    },
    /**
     * After > error display normalization >
     * Set Observer errors.
     *
     * @param { Object } errors - Formatted errors
     */
    setErrors(errors) {
      const error = defaultValidatorErrorsParser(errors)
      this.$refs.observer.setErrors(error)
    },
  },
}
</script>



