<template lang="pug">
  include ./../../mixins
  +b.modal
    +b.close(
      slot="top-right"
      @click="$emit('close')"
    )
      +b.I.icon.--type_close
    +e.content
      +b.P.text.--fs_xl.--lh_xs.--color_black.--fw_bold.mb-15 {{ _("Price estimate for your order") }}
      +b.P.text.--fs_md.--color_black
        +b.SPAN.text.--fs_md.--color_black.--fw_bold {{ _("Service:") }}
        | 
        +b.SPAN.text.--fs_md.--color_black {{ serviceName }}
      +b.P.text.--fs_md.--color_black
        +b.SPAN.text.--fs_md.--color_black.--fw_bold {{ _("Car registration number:") }}
        | 
        +b.SPAN.text.--fs_md.--color_black {{ orderIno.car_registration }}
      +b.P.text.--fs_md.--color_black
        +b.SPAN.text.--fs_md.--color_black.--fw_bold {{ _("Car:") }}
        | 
        +b.SPAN.text.--fs_md.--color_black {{ orderIno.car_model }}
      +b.P.text.--fs_md.--color_black
        +b.SPAN.text.--fs_md.--color_black.--fw_bold {{ _("Postal code:") }}
        | 
        +b.SPAN.text.--fs_md.--color_black {{ orderIno.post_code }}
      +b.P.text.--fs_md.--color_black
        +b.SPAN.text.--fs_md.--color_black.--fw_bold {{ _("Total:") }}
        | 
        +b.SPAN.text.--fs_xl.--color_red.--fw_bold {{ orderIno.price }}
      +e.form
        validation-observer(
          tag="form"
          ref="observer"
          v-slot="props"
          @submit.prevent="validateBeforeSubmit"
        )
          +b.ug-row.--offset_sm
            +b.ug-cell.ug-cols
              validation-provider(
                tag="div"
                v-slot="{errors}"
                name="phone"
                rules="required"
              )
                ui-input(
                  v-model='formdata.phone',
                  type='text',
                  :errors="errors"
                  :label='_("Enter your phone number*")'
                )
            +b.ug-cell.ug-cols
              validation-provider(
                tag="div"
                v-slot="{errors}"
                name="email"
                rules="email"
              )
                ui-input(
                  v-model='formdata.email',
                  type='text',
                  :errors="errors"
                  :label='_("Enter your mail")'
                )
            +b.ug-cell.ug-cols
              validation-provider(
                tag="div"
                v-slot="{errors}"
                name="name"
              )
                ui-input(
                  v-model='formdata.name',
                  type='text',
                  :errors="errors"
                  :label='_("Full Name")'
                )
            +b.ug-cell.ug-cols
              validation-provider(
                tag="div"
                v-slot="{errors}"
                name="comment"
              )
                +b.control
                  +e.elements
                    +b.control-input.--textarea
                      +b.TEXTAREA.control-textarea(
                        v-model='formdata.comment',
                        name='comment',
                      )
                    +e.label(:class= "{'is-active': formdata.comment.length > 0}")
                      label {{ _("Your comment") }}
                  +e.meta
                    +b.SPAN.help.--type_error(:class="{'is-error': errors[0]}") {{ errors[0] }}
            validation-provider(
              tag="div"
              v-slot="{errors}"
              name="nonFieldErrors"
            )
              +b.ug-cell.ug-cols
                +b.control.mb-20(v-if="errors[0]")
                  +e.meta
                    +b.SPAN.help.--type_error(:class="{'is-error': errors[0]}") {{ errors[0] }}
            +b.ug-cell.ug-cols
              +b.A.button(@click.prevent="validateBeforeSubmit") {{ _("Send a request") }}
      +b.P.text.--fs_xl.--lh_xs.--color_black.--fw_bold.mb-10 {{ _("Have a question?") }}
      +b.P.text.--fs_md.--lh_xxs.--color_black
        +b.SPAN.text.--color_black.--fs_md.--lh_xxs {{ _("Please Call Us on") }}
        | 
        +b.A.text.--color_black.--fs_md.--lh_xxs.--fw_bold(:href='"tel" + _("07501793857")') {{ _("07501793857") }}
        | 
        +b.SPAN.text.--color_black.--fs_md.--lh_xxs {{ _("or provider us phone number, email that we are able to contact you.") }}
</template>

<script>
import mixinValidate from '@/js/mixins/formValidate'
import mixinErrors from '@/js/mixins/showErrors'
import InfoModal from '@/js/components/Modals/InfoModal'
export default {
  props: {
    url: String,
    quoteInfo: {},
    orderIno: {},
    serviceName: String,
  },
  mixins: [
    mixinValidate,
    mixinErrors,
  ],
  data() {
    return {
      formdata: {
        comment: ''
      }
    }
  },
  methods: {
    send() {
      let formdata = Object.assign(this.quoteInfo, this.formdata)
      this.api.post(this.url, formdata).then(res => {
        this.$emit('close')
        this.$modal.show(InfoModal, {
          title: this._("Thansk for request2. We will contact you."),
        }, {
          height: 'auto',
          transition: "nice-modal-fade",
          clickToClose: false,
          scrollable: true
        })
      }).catch(error => {
        let {data} = error.response
        this.showErrors(data)
      })
    }
  },
}
</script>



