<template lang="pug">
  include ./../../../../mixins
  +b.cabinet-modal
    +e.close(
      @click.prevent="$emit('close')"
    )
      +b.icon--type_close
    +b.bg--white.pa--f_sm.--f_md-md.--f_lg-lg
      validation-observer(
        ref="observer"
        tag="form"
        class="form"
      )
        +b.ma--b_sm.TEXT-BEMTO(
          :fw="'bold'"
          :lh="'initial'"
          :fs="'4xl'"
          :color="'black'"
          :tag="'p'"
        ) Сhange E-mail
        validation-provider(
          v-slot="{ errors }"
          name="nonFieldErrors"
        )
          +b.ma--b_sm.TEXT-BEMTO(
            :fw="'medium'"
            :lh="'small'"
            :fs="'xs'"
            :color="'red'"
            :tag="'p'"
          ) {{ errors[0] }}
        +b.ug-row--apearance_spaced.--space_md
          +b.ug-cell.ug-cols--12
            +b.input--modal.D-CONTROL-INPUT(
              v-model="formData.email"
              :input-label="'Email address *'"
              :placeholder="'Email address *'"
              :rules="{required: true, email: true}"
              :name="'email'"
              :type="'text'"
              id="email"
            )
          +b.ug-cell.ug-cols--12
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'gray1'"
              :tag="'p'"
            ) We will send a message to this Email with a link to confirm a new Email
          +b.ug-cell.ug-cols--12
            +b.button--height_xl.BUTTON-BEMTO(
              :disabled="isRequest"
              @event="validateBeforeSubmit(formData)"
            )
              +e.load.IMG(
                src="/static/load.gif"
                v-if="isRequest"
              )
              +e.text(
                v-if="!isRequest"
              ) CHANGE
</template>

<script>
import FormMixin, { defaultValidatorErrorsParser } from '@/js/mixins/FormMixin'
import ToggleModalMixin from '@/js/mixins/ToggleModalMixin'
import { postEmailChangeAPI } from '@/js/services/cabinet'
import { mapGetters } from 'vuex'

export default {
  name: 'EmailChange',
  mixins: [
    FormMixin,
    ToggleModalMixin,
  ],
  data() {
    return {
      /**
       * Component state.
       */
      formData: {
        email: null,
      },
      isRequest: false,
    }
  },
  computed: {
    ...mapGetters('cabinet', [
      'emailCommon',
    ]),
  },
  watch: {
    /**
     * Hook with updating personal
     * data, listening to the data,
     * if any, then we react.
     */
    emailCommon: {
      immediate: true,
      deep: true,
      handler(data) {
        /**
         * If any, then we react.
         */
        if (data) {
          this.prefillData(data)
        }
      },
    },
  },
  methods: {
    /**
     * Submission handler, if validated.
     * @param { Object } data - Send data.
     */
    async send(data) {
      await postEmailChangeAPI.execute(
        {}, data
      )
        .then(response => {
          const OPTIONS = {
            title: response.data.item.detail,
          }
          /**
           * Show success message.
           */
          this.toggleModalMixin(
            'SuccessCommon', OPTIONS
          )
        })
    },
    /**
     * Pre-filling keys with personal
     * user data.
     */
    prefillData(data) {
      Object.keys(data).forEach(key => {
        this.formData[key] = data[key]
      })
    },
    /**
     * After > error display normalization >
     * Set Observer errors.
     *
     * @param { Object } errors - Formatted errors
     */
    setErrors(errors) {
      const error = defaultValidatorErrorsParser(errors)
      this.$refs.observer.setErrors(error)
    },
  },
}
</script>



