<script>
export default {
  name: 'ToggleMixinModal',
  props: {
    componentName: {
      type: String,
      default: 'EmptyComponent',
    },
    modalContainer: {
      type: String,
      default: 'cabinet-modal--container',
    },
    modalClass: {
      type: String,
      default: 'cabinet-modal--size_sm',
    },
    objectItem: {
      required: false,
    },
  },
  render() {
    /**
     * Template render.
     */
    return this.$scopedSlots.default({
      handler: this.toggleModalHandler,
    })
  },
  methods: {
    /**
     * Component display handler.
     */
    toggleModalHandler() {
      this.$modal.show(() => import('../SampleModal'), {
        component: this.componentName,
        objectItem: this.objectItem,
      }, {
        classes: `cabinet-modal ${this.modalContainer} ${this.modalClass}`,
      })
    },
  },
}
</script>



