<template lang="pug">
  component(
    is="a"
    :href="href"
    :download="download"
    :class="genetatedClass"
  )
    slot
</template>

<script>
import BemtoMixin from './mixins/BemtoMixin'

export default {
  name: 'LinkBemto',
  mixins: [
    BemtoMixin,
  ],
  props: {
    href: String,
    block: {
      default: () => 'text',
    },
  },
}
</script>



