<template lang="pug">
  include ./../../mixins
  +b.table-invoice
    +e.TABLE
      +e.thead.THEAD
        +e.TR
          +e.th--id.TH
            +e.info-wrapper--pointer(
              @click.prevent="changeSort('id')"
            )
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) INVOICE ID
              +e.icon.icon--type_order
          +e.th.TH
            +e.info-wrapper
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) FILE
          +e.th.TH
            +e.info-wrapper
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) UPLOADED FILE NAME
          +e.th.TH
            +e.info-wrapper
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) DATE UPLOADED
          +e.th.TH
            +e.info-wrapper
              +b.TEXT-BEMTO(
                :fw="'bold'"
                :lh="'small'"
                :fs="'xs'"
                :color="'black'"
                :tag="'p'"
              ) STATUS
      +e.TBODY
        +e.TR(
          v-for="(item, index) in objectList"
          :key="`${item.id}/${index}`"
        )
          +e.td--id.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.id }}
          +e.td.TD
            +e.info-wrapper
              +b.LINK-BEMTO(
                :cfg="'underline'"
                :fw="'medium'"
                :lh="'small'"
                :fs="'xs'"
                :color="'blue'"
                :href="item.attachmentFile"
                :download="item.attachmentFile"
                :tag="'a'"
              ) {{ item.fileName }} {{ item.fileSize }}
              +e.icon.icon--type_save
          +e.td.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.fileName }}
          +e.td.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.createdAt }}
          +e.td.TD
            +b.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'xs'"
              :color="'black'"
              :tag="'p'"
            ) {{ item.withdrawStatus }}
</template>

<script>
export default {
  name: 'InvoiceHistoryTable',
  props: [
    'objectList',
  ],
  data() {
    return {
      ordering: '-',
    }
  },
  methods: {
    setOrderHandler(key) {
      if (this.ordering.includes('-')) {
        this.ordering = key
      } else {
        this.ordering = `-${key}`
      }
    },
    async changeSort(key) {
      await this.setOrderHandler(key)
      this.$emit('ordering', this.ordering)
    },
  },
}
</script>



