<template lang="pug">
  include ./../../../../mixins
  +b.cabinet-modal
    +e.close(
      @click.prevent="$emit('close')"
    )
      +b.icon--type_close
    +b.bg--white.pa--f_sm.--f_md-md.--f_lg-lg
      validation-observer(
        ref="observer"
        tag="form"
        class="form"
      )
        +b.ma--b_sm.TEXT-BEMTO(
          :fw="'bold'"
          :lh="'initial'"
          :fs="'4xl'"
          :color="'black'"
          :tag="'p'"
        ) Change password
        validation-provider(
          v-slot="{ errors }"
          name="nonFieldErrors"
        )
          +b.ma--b_sm.TEXT-BEMTO(
            :fw="'medium'"
            :lh="'small'"
            :fs="'xs'"
            :color="'red'"
            :tag="'p'"
          ) {{ errors[0] }}
        +b.ug-row--apearance_spaced.--space_md
          +b.ug-cell.ug-cols--12
            +b.input--modal.D-CONTROL-INPUT(
              v-model="formData.oldPassword"
              :input-label="'Current password *'"
              :placeholder="'Current password *'"
              :rules="'required|min:8|max:16'"
              :name="'oldPassword'"
              :type="'password'"
              id="oldPassword"
            )
          +b.ug-cell.ug-cols--12
            +b.input--modal.D-CONTROL-INPUT(
              v-model="formData.newPassword1"
              :input-label="'Create new password *'"
              :placeholder="'Create new password *'"
              :rules="'required|min:8|max:16'"
              :name="'newPassword1'"
              :type="'password'"
              id="newPassword1"
            )
          +b.ug-cell.ug-cols--12
            +b.ma--b_sm.TEXT-BEMTO(
              :fw="'medium'"
              :lh="'small'"
              :fs="'1md'"
              :color="'black'"
              :tag="'p'"
            ) Your new password must be at least 8 characters long and contain at least 6 unique characters
          +b.ug-cell.ug-cols--12
            +b.input--modal.D-CONTROL-INPUT(
              v-model="formData.newPassword2"
              :input-label="'Confirm new password *'"
              :placeholder="'Confirm new password *'"
              :rules="'required|min:8|max:16|confirmed:newPassword1'"
              :name="'newPassword2'"
              :type="'password'"
              id="newPassword2"
            )
          +b.ug-cell.ug-cols--12
            +b.button--height_xl.BUTTON-BEMTO(
              :disabled="isRequest"
              @event="validateBeforeSubmit(formData)"
            )
              +e.load.IMG(
                src="/static/load.gif"
                v-if="isRequest"
              )
              +e.text(
                v-if="!isRequest"
              ) SUBMIT
</template>

<script>
import FormMixin, { defaultValidatorErrorsParser } from '@/js/mixins/FormMixin'
import ToggleModalMixin from '@/js/mixins/ToggleModalMixin'
import { postPassChangeAPI } from '@/js/services/cabinet'

export default {
  name: 'PasswordChange',
  mixins: [
    FormMixin,
    ToggleModalMixin,
  ],
  data() {
    return {
      /**
       * Component state.
       */
      formData: {
        newPassword1: null,
        newPassword2: null,
        oldPassword: null,
      },
      isRequest: false,
    }
  },
  methods: {
    /**
     * Submission handler, if validated.
     * @param { Object } data - Send data.
     */
    async send(data) {
      await postPassChangeAPI.execute(
        {}, data
      )
        .then(response => {
          const OPTIONS = {
            title: response.data.item.detail
          }
          /**
           * Show success message.
           */
          this.toggleModalMixin(
            'SuccessCommon', OPTIONS
          )
        })
    },
    /**
     * After > error display normalization >
     * Set Observer errors.
     *
     * @param { Object } errors - Formatted errors
     */
    setErrors(errors) {
      const error = defaultValidatorErrorsParser(errors)
      this.$refs.observer.setErrors(error)
    },
  },
}
</script>



