<template lang="pug">
  include ./../../../mixins
  div
    router-module()
    +b.pa--t_md.--b_md.SECTION
      +b.ug-container--variant_large
        +b.ug-row--apearance_spaced.--space_md
          +b.ug-cell.ug-cols--12.--9-mac
            +b.ug-row--apearance_spaced.--space_md.--justify_center
              +b.ug-cell.ug-cols--12.--6-md.--4-lg
                day-stats-panel(
                  :object-item="weekInfo"
                )
              +b.ug-cell.ug-cols--12.--6-md.--4-lg
                send-message-cabinet()
              +b.ug-cell.ug-cols--12.--6-md.--4-lg
                numbers-panel(
                  :object-item="weekInfo"
                )
              +b.ug-cell.ug-cols--12
                +b.pa--t_1sm.--t_md-md.--r_1sm.--r_md-md.--l_1sm.--l_md-md.--b_1sm.bg--white
                  +b.ma--b_xs.--b_md-md.TEXT-BEMTO(
                    :fw="'bold'"
                    :lh="'small'"
                    :fs="'xl'"
                    :color="'black'"
                    :tag="'p'"
                  ) Orders (Monday to Sunday)
                  statistics-table(
                    v-if="Object.keys(statisticsItems).length"
                    :object-list="statisticsItems"
                    @ordering="setOrderingHandler"
                  )
                  +b.text.pa--b_xs-md(
                    v-if="!Object.keys(statisticsItems).length"
                  ) No results
            +b.ma--t_md.PAGINATION-MODULE(
              :pagination="pagination"
              :extra="1"
              :need-add-btns="true"
              @update-limit="setPaginationLimitHandler"
              @input="pageСhangedHandler($event)"
            )
          +b.ug-cell.ug-cols--12.--3-mac
            quick-qoute-state-form(
              :services-items="servicesItems"
              :object-item="weekInfo"
            )
</template>

<script>
import {
  getStatisticsAPI,
  getInfoWeekAPI,
  getServicesAPI,
} from '@/js/services/cabinet'

export default {
  name: 'StatisticsCabinet',
  created() {
    this.getStatisticsAPI()
    this.getInfoWeekAPI()
    this.getServicesAPI()
  },
  data() {
    return {
      formData: {
        ordering: null,
      },
      statisticsItems: {},
      paginationData: {},
      pagination: {},
      weekInfo: {},
      servicesItems: [],
    }
  },
  methods: {
    getStatisticsAPI(OPTIONS) {
      return getStatisticsAPI.execute(OPTIONS)
        .then(response => response.json())
        .then(response => {
          this.statisticsItems = response.items
          this.pagination = response.pagination
        })
    },
    getInfoWeekAPI() {
      return getInfoWeekAPI.execute({})
        .then(response => {
          this.weekInfo = response.data.item
        })
    },
    getServicesAPI() {
      return getServicesAPI.execute({})
        .then(response => {
          this.servicesItems = response
        })
    },
    async preperedBeforeAPI() {
      const OPTIONS = {
        ...this.formData,
        ...this.paginationData
      }
      await this.getStatisticsAPI(
        OPTIONS
      )
    },
    async setPaginationLimitHandler(value) {
      this.paginationData.limit = value
      this.paginationData.offset = 0
      await this.preperedBeforeAPI()
    },
    async pageСhangedHandler(data) {
      this.paginationData = data
      await this.preperedBeforeAPI()
    },
    async setOrderingHandler(value) {
      this.formData.ordering = value
      await this.preperedBeforeAPI()
    },
  },
}
</script>



