<template lang="pug">
  component(
    is="button"
    :disabled="disabled"
    :class="genetatedClass"
    @click.prevent="$emit('event', $event)"
  )
    slot
</template>

<script>
import BemtoMixin from './mixins/BemtoMixin'

export default {
  name: 'ButtonBemto',
  mixins: [
    BemtoMixin,
  ],
  props: {
    block: {
      default: () => 'button',
    },
  },
}
</script>



