const DEFAULT_LANGUAGE = 'ru'
export function prefixLanguage(url = '') {
  const length = 2
  const path = new URL(window.location)
  const lang = path.pathname.split('/')[1]
  return length >= lang.length && DEFAULT_LANGUAGE !== lang && '' !== lang ? `/${lang}${url}` : url
}



